        text: "Стране требовались деньги – на содержание армии, на восстановление промышленности, на поддержку мирового революционного движения. С середины 1920-х стали активно использоваться принудительные займы и заниженные цены на зерно и завышенные - на промышленные товары. Как следствие, промышленные товары, если рассчитать их стоимость в пудах пшеницы, оказались в несколько раз дороже, чем до войны, несмотря на более низкое качество. Образовалось явление, которое было названо «ножницами цен». Крестьяне перестали продавать зерно свыше того, что им было нужно для уплаты налогов. Первый кризис сбыта промышленных товаров возник осенью 1923 года. В 1924–1925 хозяйственном году случился кризис заготовок. Наконец, в 1927–1928 году - новый кризис: не удалось собрать даже самого необходимого",
