        text: "Чтобы сломить сопротивление крестьян, не желавших отдавать свой хлеб, сталинские экономисты предложили программу коллективизации – создания на селе коллективных хозяйств, колхозов. Крестьяне-колхозники должны были совместно обрабатывать общую землю, сдавая продукцию государству по фиксированным ценам. Коллективизация проводилась насильственными методами: опираясь на деревенскую бедноту, большевики попросту загоняли крестьян в колхозы. Коллективизация изменила коренной уклад жизни основной массы населения России",
