        id: 34,
        text: "Вооруженное антиправительственное выступление августа 1917 г. под руководством Верховного главнокомандующего Л.Г. Корнилова с целью установления военной диктатуры в России. Задачи: упразднение Советов на фронте и в тылу, запрет деятельности профсоюзов и фабзавкомов, ограничение распространения печати в армии. Программа: объявить железные дороги, заводы, шахты на военном положении; создать государственный земельный фонд из удельных, казенных и части помещичьих и монастырских земель, выкупленных у их владельцев, для наделения землей солдат, «беспорочно и доблестно прошедших военную службу»",
