        id: 35,
        text: "наиболее острая форма социальной борьбы населения внутри государства. Военно-политические структуры, сформировавшиеся в дни революции, и государственные образования на территории бывшей Российской империи разделились на две группы, за которыми закрепились названия «белые» и «красные». Главной военной силой советской власти стала сформированная в 1918 г. РККА (Рабоче-Крестьянская Красная Армия).Не остались в стороне и стихийно возникшие военные и общественно-политические группы, которых называли «третья сила» (повстанческие, партизанские отряды и другие). Не остались в стороне от гражданского противостояния в России и иностранные государства, или интервенты. Война закончилась утверждением власти большевиков (советской власти).",
