    text: "Вы проявили себя на полях сражений. Новый бой в гражданской жизни: Ликбез. Кто-то пойдет учить, а кто-то учиться. Начался период НЭП. Ленин заявил: «Мы перешли к НЭП всерьез и надолго, но не навсегда! Еще будет социалистическая Россия!»",
