        text: "Народный комиссариат просвещения. В Нар­ком­про­се бы­ло со­сре­до­то­че­но управ­ле­ние об­ра­зо­ва­ни­ем, нау­кой, куль­ту­рой и про­па­ган­дой, ох­ра­ной па­мят­ни­ков. Он яв­лял­ся главным ор­га­ном по осу­ще­ст­в­ле­нию куль­тур­ной ре­во­лю­ции. Нар­ком­прос ор­га­ни­зо­вал сеть экс­пе­риментальных и опыт­но - по­ка­зательных школ. 1918-1923 гг. наркомат ре­ор­га­ни­зо­вал сис­те­му на­чаль­ных, сред­них и выс­ших учеб­ных за­ве­де­ний.",
