            text: "Крупномасштабное перемещение в начальный период войны с нацистской Германией из угрожаемой зоны в восточные регионы страны населения, промышленных предприятий, культурных и научных учреждений, запасов продовольствия, сырья и других материальных ресурсов. Эвакуация была проведена в два этапа: в 1941 году — из Белоруссии, Украины, Прибалтики, г. Москвы и г. Ленинграда; в 1942 году — из южных районов европейской части СССР. В 1941 году в глубокий тыл было вывезено 1 523 крупных предприятия: на Урал — 667, в Западную Сибирь — 244, в Восточную Сибирь — 78, в Среднюю Азию и Казахстан — 308, в Поволжье — 226.",
